@media (max-width: 576px){
  .container{
		width: 100%;
		padding: 0px 10px
	}
}
@media (min-width: 576px){
  .container{
    max-width: 540px
	}
}
@media (min-width: 768px){
  .container{
		max-width: 720px
	}
}
@media (min-width: 992px) {
  .container {
		max-width: 960px
	}
}
@media (min-width: 1200px){
  .container{
		max-width: 1170px
	}
}