.about {
	&-wrap {
		background-image: url("../img/about-bg.png");
	}

	padding: 50px 0px;
	text-align: center;

	&__left-col {
		display: inline-block;
		vertical-align: middle;
		width: 36%;
		@include lg {
			width: 39%;
			@include md {
				width: 100%;
			}
		}
	}

	&__descr {
		margin-bottom: 50px;
	}

	&__photo {
		text-align: center;
		padding-left: 80px;
		max-width: 100%;
		@include xs {
			padding-left: 0;
		}
	}

	&__right-col {
		display: inline-block;
		width: 53%;
		vertical-align: middle;
		img {
			width: 100%;
		}

		@include lg {
			width: 60%;
			@include md {
				display: none;
			}
		}
	}
}