.reservations {
	&-wrap {
		background-image: url("../img/about-bg.png");
	}

	padding: 100px 0;

	@include lg {
		padding-top: 20px;
	}

	&__imgs {
		float: left;
		width: 49%;

		@include lg {
			width: 100%;
			margin-bottom: 30px;
		}
	}

	&__img {
		width: 47.5%;

		&:first-child {
			float: left;
		}

		&:last-child {
			float: right;
		}
	}

	&__descr {
		margin-bottom: 15px;
	}

	&__content {
		float: right;
		width: 37%;
		margin-right: 70px;

		@include lg {
			width: 100%;
			margin-right: 0;
		}
	}
}

.form {

	&__line {
		margin-bottom: 25px;
		@include xs {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: 0;
		}
	}

	&__label {
		width: 36%;
		font-family: Arial;
		color: #c9a131;
		font-size: 14px;
		line-height: 1.8;
		font-weight: bold;

		@include xl {
			width: 40%;
			@include xs {
				width: 70%;
				margin-bottom: 20px;
			}
		}
	}

	&__input{
		border: 1px solid #D0CECE;
		padding: 7px 13px;
		font-family: Arial;
		color: #7c7c7c;
		width: 100%;
		font-style: italic;
		@include placeholder {
			font-family: Arial;
			color: #7c7c7c;
		}

		&:focus {
			border-color: #D7A016;
		}
	}

	&__select {
		border: 1px solid #D0CECE;
		padding: 7px 13px;
		font-family: Arial;
		color: #7c7c7c;
		width: 100%;
		background-color: #fff;
		font-style: italic;
		&-option {
			padding: 5px 10px;
			font-family: Arial;
			color: #7c7c7c;
		}
	}

	&__date {
		input {
			border: 1px solid #D0CECE;
			padding: 6px 10px;
			font-family: Arial;
			color: #7c7c7c;
			width: 100%;
			font-style: italic;
			background-color: #fff;
			font-size: 14px;
		}
		position: relative;

		&-icon {
			position: absolute;
			right: 8px;
			top: 50%;
			transform: translateY(-50%);
			color: #7D7D7D;
		}
	}

	&__label:first-child {
		float: left;
	}
	&__label:last-child {
		float: right;
	}

	&__button {
		background-color: #D8A21C;
		padding: 10px 60px;
		border: none;
		font-size: 16px;
		font-family: Arial;
		color: #fff;
		display: block;
		margin-left: auto;
		margin-right: auto;
		@include xs {
			margin-top: 20px;
		}
	}
}