.ingredients {
	&-wrap {
		background-image: url("../img/ingridient-bg.jpg");
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}
	padding: 100px 0;

	&__block {
		padding: 75px;
		width: 49%;
		@include lg {
			width: 80%;
			@include md {
				width: 100%;
				padding: 60px 20px;
				@include xs {
					padding: 50px 10px;
				}
			}
		}
		float: right;
		background-image: url("../img/block-text-bg.png");
	}

	&__descr {
		margin-bottom: 65px;
	}

	&__imgs {
		text-align: center;
	}

	&__img {
		margin: 0px 15px;
		border-radius: 50%;
		border: 5px solid rgba(#595959, 0.3);
	}
}