.menu {
	&-wrap {
		background-image: url("../img/menu-bg.png");
	}
	padding: 100px 0px;
	
	&__left-col {
		width: 38.8%;
		float: left;
		margin-left: 55px;
		@include md {
			width: 100%;
			margin-left: 0;
		}
	}
	&__right-col {
		width: 38.8%;
		float: right;
		margin-right: 55px;
		@include md {
			width: 100%;
			margin-right: 0;
		}
	}
}

.dish {
	&-section {
		width: 100%;
		display: inline-block;
		margin-bottom: 40px;
		&:last-child {
			margin-bottom: 0;
		}
		&__title {
			margin-bottom: 55px;
		}
	}

	padding: 8px 13px;
	margin-bottom: 20px;

	&_special {
		border: 1px solid rgba(#c89f2d, 0.2);
		position: relative;
		&:before {
			content: "Special";
			display: block;
			position: absolute;
			top: -23.5px;
			right: -1px;
			padding: 3px 10px;
			color: #fff;
			font-size: 14px;
			font-family: "Lato-Regular";
			background-color: rgba(#c89f2d, 0.4);
		}
	}

	&__title {
		font-family: $ff-headers;
		font-weight: normal;
		color: $fc-main-medium;
		font-size: 21px;
		float: left;
		margin-bottom: 10px;
	}

	&__descr {
		font-size: 16px;
		color: $fc-main-dark;
		width: 100%;
		float: left;
		border-top: 1px solid rgba(#c89f2d, 0.2);
		padding-top: 10px;
	}

	&__price {
		font-size: 21px;
		font-family: $ff-headers;
		color: $fc-main-medium;
		float: right
	}
}