.container{
	width: 1170px;
	margin: auto;
}

.hide {
	display: none;
}

.form__error {
	font-size: 12px;
	color: #FF0A0E;
	line-height: 1;
	font-family: Arial;
	font-weight: lighter;
	margin-top: 10px;
	height: 0px;
	transition-duration: 0.2s;
}

.button {
	font-family: $ff-main;
	font-size: 18px;
	color: $fc-main-light;
	border-style: solid;
	border-width: 1px;
	padding: 20px 50px;
	cursor: pointer;
	text-transform: uppercase;

	&_dark {
		border-color: rgba(#000,0.8);
		background-color: rgba(#000, 0.56);
		&:hover {
			background-color: rgba(#000, 0.26);
		}
	}

	&_light {
		border-color: rgba(#fff,0.8);
		background-color: rgba(#fff, 0.56);
		&:hover {
			background-color: rgba(#fff, 0.26);
		}
	}
}

.title {
	font-size: 36px;
	font-family: $ff-headers;
	font-weight: normal;
	text-align: center;
	position: relative;
	margin-bottom: 70px;
	&_medium {
		color: $fc-main-medium;
	}
	&_light {
		color: $fc-main-light;
	}
	&_with-divider-light {
		&:after {
			content: "";
			display: inline-block;
			position: absolute;
			bottom: -40px;
			left: 50%;
			margin-left: -123px;
			@include sprite($divider-light)
		}
	}
	&_with-divider-light_small {
		margin-bottom: 60px;
		&:after {
			content: "";
			display: inline-block;
			position: absolute;
			bottom: -40px;
			left: 50%;
			margin-left: -100px;
			@include sprite($divider-light_small)
		}
	}
	&_with-divider-medium {
		&:after {
			content: "";
			display: inline-block;
			position: absolute;
			bottom: -40px;
			left: 50%;
			margin-left: -123px;
			@include sprite($divider-medium)
		}
	}
}

.descr {
	font-size: 16px;
	font-family: $ff-main;
	text-align: center;
	line-height: 24px;
	&_light {
		color: $fc-main-light;
	}
	&_dark {
		color: $fc-main-dark;
	}
}