.footer {
	background-color: #121212;

	padding: 65px 0px 55px 0px;

	&-row {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}

	&__text {
		font-family: $ff-additional;
	}

	&__text-b {
		font-family: $ff-additional-bold;
	}

	&-col {
		width: 33.33%;
		@include md {
			width: 100%;
		}

		&:last-child .footer-item:last-child {
			margin-bottom: 0;
		}
	}

	&-item {
		margin-bottom: 50px;
		&:last-child {
			margin-bottom: 0px;
			@include md {
				margin-bottom: 50px;
			}
		}
	}

	&-about {
		&__title {
			font-size: 21px;
		}
	}

	&-hours {
		text-align: center;
		&__title {
			font-size: 21px;
		}
	}

	&-location {
		&__title {
			font-size: 21px;
		}
	}

	&-paiment {
		text-align: center;
		color: #fff;
		&__method-block {
			display: inline-block;
			margin: 0px 3px;
			cursor: pointer;
			&:hover {
				color: darken(#fff, 50);
			}
		}
	}

	&-socials {
		text-align: center;
		color: #fff;
		&__social {
			display: inline-block;
			margin: 0px 3px;
			cursor: pointer;
			&:hover {
				color: darken(#fff, 50);
			}
		}
	}
}