.reviews {
	&-wrap {
		background-image: url("../img/review-bg.jpg");
		background-size: cover;
		background-position: center;
	}

	padding: 100px 0;
}

.review {
	width: 66%;
	margin-left: auto;
	margin-right: auto;
	padding: 70px 70px 100px 70px;
	background-image: url("../img/block-text-bg.png");
	@include lg {
		width: 90%;
		@include md {
			width: 100%;
			padding: 50px 50px 70px 50px;
			@include xs {
				padding: 50px 20px 50px 20px;
			}
		}
	}
	&__descr {
		font-size: 25px;
		line-height: 45px;
		text-align: center;
		font-family: "GentiumBookBasic-Bold";
		color: $fc-main-light;
		position: relative;
		margin-bottom: 30px;

		@include xs {
			font-size: 20px;
			line-height: 1.6;
			margin-bottom: 20px;
		}

		&:after {
			content: "";
			@include sprite($quotes);
			display: block;
			position: absolute;
			top: -5px;
			left: -35px;
			@include xs {
				left: -15px;
				top: -25px;
			}
		}
	}

	&__author {
		font-family: $ff-main;
		font-size: 18px;
		color: $fc-main-light;
		text-align: center;
	}
}