.header {
	background-image: url('../img/header-bg.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	padding-bottom: 140px;
	padding-top: 220px;
	&__top {
		@include lg {
			//padding-right: 100px;
		}

		&-wrap {
			background-image: url("../img/header-top-bg.png");
			z-index: 99;
			background-repeat: repeat-x;
			background-position: center top;
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			min-height: 160px;
		}
	}
	&__logo {
		float: left;
		margin-top: 10px;
		position: relative;
		margin-left: -40px;
		img {
			width: 100%;
		}
		@media (max-width: 1300px) {
			margin-left: 0;
		}
		@include xs {
			width: 40%;
		}
		
	}
	&__socials{
		float: right;
		margin-top: 52px;
		margin-left: 10px;
		@include xs {
			margin-top: 55px;
		}
	}
	&__social{
		display: inline-block;
		vertical-align: middle;
		margin-right: 13px;
		font-size: 21px;
		color: #dab33e;
		transition: 0s;

		@include xl {
			font-size: 30px;
			margin-right: 20px;
			@include xs {
				font-size: 20px;
			}
		}

		&:last-child {
			margin-right: 0;
		}
		&:hover{
			color: darken(#dab33e, 30)
		}
	}
	&__nav {
		float: right;
	}

	&__title {
		font-family: $ff-headers;
		font-size: 90px;
		color: $fc-main-light;
		text-align: center;
		position: relative;
		margin: 0px 0px 110px 0px;
		font-weight: normal;

		@include lg {
			font-size: 70px;
			@include md {
				font-size: 50px;
				@include xs {
					font-size: 40px;
				}
			}
		}

		&:after {
			content: "";
			display: inline-block;
			position: absolute;
			bottom: -70px;
			left: 50%;
			margin-left: -120px;
			@include sprite($divider-header)
		}
	}
	&__buttons {
		text-align: center;
	}
	&__button {
		&:first-child{
			margin-right: 27px;
		}
		@include sm {
			display: block;
			margin-left: auto;
			margin-right: auto;
			&:first-child {
				margin-right: auto;
				margin-bottom: 20px;
			}
		}
	}
}

.nav {
	@include xl {
		position: fixed;
		// background-color: rgba(#fff, 0.5);
		background-color: #fff;
		right: 0;
		left: 0;
		top: 0;
		bottom: 0;
		display: none;
		flex-direction: column;
		align-items: center;
		justify-content: space-around;

		&-mobile_active{
			display: flex;
		}
	}
	&__item {
		padding: 50px 10px;
		display: inline-block;
		position: relative;
		color: $fc-main-light;
		font-size: 21px;
		font-family: $ff-nav;
		margin-right: 25px;

		@include xl {
			font-size: 30px;
			color: #424242;
			padding: 0;
			height: 50px;
			line-height: 50px;
			display: block;
			margin-right: 0;
		}

		&:last-child {
			margin-right: 0;
		}
		&:after{
			content: "";
			display: block;
			position: absolute;
			height: 3px;
			top: 0;
			left: 0;
			right: 0;
			margin: auto;
			width: 0%;
			background-color: #CEC8BC;
			transition: 0.2s;
		}
		&:hover:after{
			width: 100%;
		}
		&_active{
			&:after {
				content: "";
				display: block;
				position: absolute;
				height: 3px;
				top: 0;
				width: 100%;
				background-color: #CEC8BC;
			}
		}
	}
}

.nav-burger-wrap {
	float: right;
	margin-top: 40px;
	margin-left: 20px;
}

.nav-burger {
	display: none;
	z-index: 50;
	cursor: pointer;
	width: 60px;
	height: 60px;
	//position: absolute;
	position: relative;

	// right: 20px;
	// top: 37px;

	@include xl {
		display: inline-block;
	}

	&__line {
		width: 90%;
		height: 5px;
		background-color: #424242;
		display: block;
		@include center;
		transition: .5s;

		//box-shadow: 0px 0px 1px #424242;

		&:before {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 5px;
			background-color: #424242;
			top: -18px;
			transition: .5s;

			//box-shadow: 0px 0px 1px #424242;
		}

		&:after {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 5px;
			background-color: #424242;
			top: 18px;
			transition: .5s;

			//box-shadow: 0px 0px 1px #424242;
		}
	}

	&_active {
		.nav-burger__line {
			background-color: rgba(0,0,0,0);
			box-shadow:none;

			&:before {
				top: 0;
				transform: rotate(45deg);
			}
			&:after {
				top: 0;
				transform: rotate(135deg);
			}
		}
	}
}